import React, {Component} from 'react';
import { TabSwitch, TabCont, Tabs, Tab }  from '../../../src/index';
import './tabswitchdemo.styl';

class TabSwitchDemo extends Component {
  constructor(props) {
    super(props);
  }

  onChange(i) {
    console.log('on change', i);
  }

  render() {
    return (
      <div className="tab-switch-demo">
        <TabSwitch onChange={this.onChange.bind(this)} 
              activeIndex={1} >
          <Tabs className="home-nav">
            <Tab>
              <div className="nav-item">
                
              </div>
            </Tab>
            <Tab>
              <div className="nav-item">
                
              </div>
            </Tab>
             <Tab>
              <div className="nav-item">
                
              </div>
            </Tab>
          </Tabs>
          <div className="tabs-cont">
            
            <TabCont>
              <div></div>
            </TabCont>

            <TabCont>
              <div></div>
            </TabCont>

            <TabCont>
              <div></div>
            </TabCont>

          </div>
        </TabSwitch>
      </div>
    );
  }
}

export default TabSwitchDemo;


